<template>
    <div class=" m-auto center">
      <div v-for="(article, index) in articles" :key="index" class="float-left">
        <article-preview :article="article"></article-preview>
      </div>
    </div>
</template>
<script>
import articlePreview from './articlePreview'
export default {
  data () {
    return { articles: [] }
  },
  created () {
    this.articles = JSON.parse(localStorage.getItem('recommend_article'))
  },
  components: { 'article-preview': articlePreview }
}
</script>



