<template>
  <div>
    <div style="height:5rem;">
      <navigator @switchPage="switchPage"></navigator>
    </div>
    <component :is="currentComponent"></component>
  </div>
</template>
<script>
import navigator from './common/navigator'
import homePage from './homePage/homePage'
import searchArticle from './searchArticle'
export default {
  name: 'mySiteRouter',
  data () {
    return {
      currentComponent: 'home-page'
    }
  },
  components: { navigator: navigator, 'home-page': homePage, 'search-article': searchArticle },
  methods: {
    reload () {},
    switchPage (pageName) {
      this.currentComponent = pageName
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



