<template>
    <div v-show="show" class="w-100 h-100 position-fixed" style="z-index:9999;margin-top:0px;background-color:rgba(240,240,240,0.5);">
        <button type="button" class="close p-2" aria-label="Close" @click="closeMyPage()">
            <span aria-hidden="true">&times;</span>
        </button>
        <div class="card p-3 m-5 h-100">
            <div>
                <img class="rounded-circle align-items-baseline" style="height:2.5rem;" src="./../../static/img/myIcon.png">
                <div>
                    <span class="badge badge-secondary mr-1" v-for="(badge, index) in me.tags" :key="index">{{badge}}</span>
                </div>
                <div>
                    <span class="font-weight-light form-control-plaintext" style="font-size:0.8rem;">
                        {{me.label}}
                    </span>
                </div>
                <div>
                    <i class="iconfont icon-github" @click="jump('https://github.com/Lucouse')"></i>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      me: {}
    }
  },
  created () {
    this.me = JSON.parse(localStorage.getItem('about_me'))
  },
  methods: {
    display () {
      this.show = true
    },
    closeMyPage () {
      this.show = false
    },
    jump (pathParam) {
      window.open(pathParam, '_blank')
    }
  }
}
</script>
<style>
</style>



