<template>
    <div id="carouselExampleIndicators" class="carousel slide bg-white w-75 m-auto" data-ride="carousel">
        <ol class="carousel-indicators">
            <li data-target="#carouselExampleIndicators" v-for="(slide, index) in slides" :key="index" :data-slide-to="index" :class="index===0?'active':''"></li>
        </ol>
        <div class="carousel-inner">
            <div @click="jumpToArticle(slide.code)" class="carousel-item w-100" v-for="(slide, index) in slides" :key="index" :class="index===0?'active':''">
                <img class="center-block" style="height:10rem;" :src="'./../../../static/img/carousel/'+slide.img" :alt="slide.title">
                <div class="carousel-caption d-none d-md-block">
                    <h5>{{slide.title}}</h5>
                    <p>{{slide.description}}</p>
                </div>
            </div>
        </div>
        <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="sr-only">{{$t("previous")}}</span>
        </a>
        <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="sr-only">{{$t("next")}}</span>
        </a>
    </div>
</template>
<script>
export default {
  data () {
    return { slides: [] }
  },
  created () {
    this.slides = JSON.parse(localStorage.getItem('hot_recommend_article'))
  },
  methods: {
    jumpToArticle (code) {
      window.open('#/article/' + code, '_blank')
    }
  }
}
</script>
<style>
</style>



