<template>
    <div id="settings" class="modal fade" tabindex="-1" role="dialog">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{$t("settings")}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div>
                        <ul class="nav nav-tabs">
                            <li class="nav-item">
                                <a class="nav-link active" href="#">{{$t('normal')}}</a>
                            </li>
                        </ul>
                        <div class="card-body">
                            <form>
                                <div class=" form-group">
                                    <div class="row">
                                    <label class="col-sm-4">{{$t("language")}}</label>
                                    <select class="form-control form-control-sm col-sm-4" v-model="locale">
                                        <option value="zh">中文</option>
                                        <option value="ja">日本語</option>
                                        <option value="en">English</option>
                                    </select>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>

                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-primary" @click="save()">{{$t("save")}}</button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">{{$t("cancel")}}</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      locale: ''
    }
  },
  created () {},
  methods: {
    modal () {
      $('#settings').modal({ backdrop: 'static' })
      this.locale = localStorage.getItem('locale')
    },
    save () {
      localStorage.setItem('locale', this.locale)
      this.$i18n.locale = this.locale
      this.$emit('newMessage', { title: this.$t('settings'), time: new Date(), context: this.$t('settings_saved') })
    }
  }
}
</script>



