import Vue from 'vue'
import Router from 'vue-router'
import mySiteRouter from '@/components/mySiteRouter'
import article from '@/components/article'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mySiteRouter',
      component: mySiteRouter
    },
    {
      path: '/article/:id',
      name: 'article',
      component: article
    }
  ]
})



