<template>
    <div>
        <nav class="navbar navbar-expand-lg navbar-light bg-light position-fixed w-100"  style="z-index:999;">
            <div class="navbar-header">
                <a class="navbar-brand" href="#">
                    <img class=" rounded-circle align-items-baseline" @click="openMyPage()" style="height:2.5rem;" src="./../../../static/img/myIcon.png">
                </a>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarToggleExternalContent" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
            </div>
            <div class="collapse navbar-collapse" id="navbarToggleExternalContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item active" v-for="(nav, index) in navs" v-bind:key="index">
                        <router-link class="nav-link" to="/">{{ $t('home') }}</router-link>
                    </li>
                </ul>
            </div>
            <form class="form-inline">
                <i class="iconfont icon-search ml-2" @click="searchPage('search-article')"></i>
                <i class="iconfont icon-setting ml-2" @click="settings()"></i>
                <i class="iconfont icon-xiaoxi1 ml-2" @click="message()"></i>
            </form>
        </nav>
        <my-page ref="myPage" @newMessage = "newMessage"></my-page>
        <search-article ref="searchArticle" @newMessage = "newMessage"></search-article>
        <settings ref="settings" @newMessage = "newMessage"></settings>
        <messages ref="messages"></messages>
    </div>
</template>
<script>
import myPage from './../myPage'
import searchArticle from './../searchArticle'
import settings from './../settings'
import messages from './../messages'
export default {
  date () {
    return { navs: [] }
  },
  components: { 'my-page': myPage, 'search-article': searchArticle, settings, messages },
  created () {
    this.navs = [{ text: 'Home', code: 'home-page' }]
  },
  methods: {
    searchPage (pageName) {
      this.$refs.searchArticle.display()
    },
    openMyPage () {
      this.$refs.myPage.display()
    },
    settings () {
      this.$refs.settings.modal()
    },
    message () {
      this.$refs.messages.toggle()
    },
    newMessage (message) {
      this.$refs.messages.newMessage(message)
    }
  }
}
</script>
<style>
</style>



