<template>
    <div class="h-100 w-25 mt-5 position-fixed align-middle" style="z-index:9999;right:0;" v-show="allMessageShow || newMessageShow">
        <div class="h-100 w-100 card" style="background-color:rgba(240,240,240,0.5);" v-show="allMessageShow">
            <!-- All message-->
            <button class="btn btn-secondary w-100" @click="clearAllMessage">清空</button>
            <div class="p-4 pre-scrollable h-100">
                <div v-for="(message, index) in messages" :key="index" class="card card-body w-100 m-2">
                    <button type="button" class="close position-absolute m-2" style="right:0px;top:0px;" aria-label="Close" @click="deleteMessage(index, false)">
                        <span aria-hidden="true">&times;</span>
                    </button>
                    <h5>{{message.title}}</h5>
                    <span>{{message.time}}</span>
                    <p>{{message.context}}</p>
                </div>
            </div>
        </div>
        <!-- new message-->
        <div class="position-absolute mb-5" style="bottom:1rem;" v-show="newMessageShow">
            <div v-for="(newMessage, index) in newMessages" :key="index" class="card card-body m-3" style="width:20rem;height:10rem;box-shadow: 3px 3px 6px rgb(126,126,126);">
                <button type="button" class="close position-absolute m-2" style="right:0px;top:0px;" aria-label="Close" @click="deleteMessage(index, true)">
                    <span aria-hidden="true">&times;</span>
                </button>
                <h5>{{newMessage.title}}</h5>
                <span>{{newMessage.time}}</span>
                <p>{{newMessage.context}}</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      messages: [],
      newMessages: [],
      allMessageShow: false,
      newMessageShow: false
    }
  },
  created () {},
  methods: {
    toggle () {
      // eslint-disable-next-line
      this.allMessageShow = this.allMessageShow == true ? false : true
    },
    newMessage (messageParam) {
      this.newMessageShow = true
      this.messages.push(messageParam)
      this.newMessages.push(messageParam)
      setTimeout(() => {
        this.newMessages.splice(this.newMessages.length - 1, 1)
        this.newMessageShow = false
      }, 2000)
    },
    displayAllMessage () {},
    clearAllMessage () {
      this.messages.splice(0, this.messages.length)
    },
    deleteMessage (index, isNew) {
      if (isNew) {
        this.newMessages.splice(index, 1)
      } else {
        this.messages.splice(index, 1)
      }
    }
  }
}
</script>



